import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { OpenProgramBookingListComponent } from './open-program-booking-list.component';
import { FilterOpenProgramBookingListComponent } from './filter-open-program-booking-list/filter-open-program-booking-list.component';
import { OpenProgramBookingListResolver } from './open-program-booking-list.resolver';
import { EditOpenProgramBookingItemComponent } from './edit-open-program-booking-item/edit-open-program-booking-item.component';


const routes: Routes = [
  { path: 'list', component: OpenProgramBookingListComponent,
    children: [
      { path: 'filter', component: FilterOpenProgramBookingListComponent,
        resolve: {
          OpenProgramBookingListResolver
        }
      },
      { path: ':type/:id', component: EditOpenProgramBookingItemComponent,
        resolve: {
          OpenProgramBookingListResolver
        }
      }
    ]
  },
  { path: '', redirectTo: 'list', pathMatch: 'full' }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class OpenProgramBookingListRoutingModule { }
