import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Subject } from 'rxjs';

import { OpenProgramBookingListComponent } from 'src/app/modules/admin/open-program/open-program-booking-list/open-program-booking-list.component';

import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'app-filter-open-program-booking-list',
  templateUrl: './filter-open-program-booking-list.component.html',
  styleUrls: ['./filter-open-program-booking-list.component.scss']
})
export class FilterOpenProgramBookingListComponent implements OnInit {

  program = {}

  constructor(
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private _changeDetectorRef: ChangeDetectorRef,
    private _openProgramBookingListComponent: OpenProgramBookingListComponent,
    private sharedService:SharedService
  ) { }

  goBack() {
    this._openProgramBookingListComponent.matDrawer.close();
    this.sharedService.setMatDrawerId(null)
    this._router.navigateByUrl('/open-programs/open-booking');
  }

  ngOnInit(): void {

    this.sharedService.matdraweridcast.subscribe((res:any) => {

      if(res != null) {
        this._openProgramBookingListComponent.matDrawer.open();
      }

    })

  }

}
