import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class OpenProgramBookingListService {

  private OpenProgramBookingItemData = new BehaviorSubject<any>({});
  openprogrambookingitemdatacast = this.OpenProgramBookingItemData.asObservable();

  constructor() { }

  getOpenProgramBookingItemData(){
    return this.OpenProgramBookingItemData.value
  }

  setOpenProgramBookingItemData(value){
    this.OpenProgramBookingItemData.next(value);
  }

}
