import { Component, Input, Output, OnInit, ViewEncapsulation, ViewChild, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';

import { Observable, BehaviorSubject, Subscription, Subject } from 'rxjs';
import { filter, map, startWith, debounceTime, takeUntil } from 'rxjs/operators';
import { FormControl } from '@angular/forms';

import { MatDrawer } from '@angular/material/sidenav';

import { BatchService } from 'src/app/api/controllers/Batch';
import { StyleService } from 'src/app/api/controllers/Style';

import { OpenProgramBookingListService } from './open-program-booking-list.service';

import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalsService } from 'src/app/modules/ui/modals/modals.service';

import * as moment from 'moment';

@Component({
  selector: 'open-program-booking-list',
  templateUrl: './open-program-booking-list.component.html',
  styleUrls: ['./open-program-booking-list.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class OpenProgramBookingListComponent implements OnInit {

  isDataLoaded:boolean = true;

  @ViewChild('matDrawer', {static: true})
  matDrawer: MatDrawer;

  drawerMode: 'over';
  private _unsubscribeAll: Subject<any>;

  _programList:any;
  fullProgramList:any = [];

  searchData:FormControl;

  search:string = '';
  deleteSubscribe:Subscription;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 5;

  groupData: string = "";
  allData: any;

  programBatchList = []
  programStyleList = []

  constructor(
    private _changeDetectorRef: ChangeDetectorRef,
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private openProgramBookingListService: OpenProgramBookingListService,
    private batchService: BatchService,
    private styleService: StyleService,
    private sessionService: SessionService,
    private sharedService:SharedService,
    private modalsService: ModalsService
  ) {

    // Set the private defaults
    this._unsubscribeAll = new Subject();

    this._programList = new BehaviorSubject(null);
    this.searchData = new FormControl('');

  }

  get programs$(): Observable<any>{
    return this._programList.asObservable();
  }

  onBackdropClicked() {

    // Get the current activated route
    let route = this._activatedRoute;

    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    this.sharedService.setMatDrawerId(null);
    this._router.navigate(['/open-programs/open-booking'], { relativeTo: route })
    this.openProgramBookingListService.setOpenProgramBookingItemData({})
    this.matDrawer.close();

    // Mark for check
    this._changeDetectorRef.markForCheck();

  }

  viewOpenProgramBookingItem(program) {

    // Get the current activated route
    let route = this._activatedRoute;

    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    this.sharedService.setMatDrawerId(program.programPackageId)
    this.openProgramBookingListService.setOpenProgramBookingItemData(program);
    this._router.navigate(['view', program.programPackageId], { relativeTo: route })

    //Mark for check
    this._changeDetectorRef.markForCheck();

  }

  editOpenProgramBookingItem(program) {

    // Get the current activated route
    let route = this._activatedRoute;

    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    this.sharedService.setMatDrawerId(program.programPackageId)
    this.openProgramBookingListService.setOpenProgramBookingItemData(program);
    this._router.navigate(['edit', program.programPackageId], { relativeTo: route })

    //Mark for check
    this._changeDetectorRef.markForCheck();

  }

  deleteOpenProgramBookingItem(id) {
    this.modalsService.showDeleteConfirmModal(id)
  }

  filterOpenProgram() {

    // Get the current activated route
    let route = this._activatedRoute;

    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    let id = this.sharedService.guid();
    this.sharedService.setMatDrawerId(id);

    this._router.navigate(['filter'], { relativeTo: route })

    //Mark for check
    this._changeDetectorRef.markForCheck();

  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  getDate(date) {
    return moment(date).format('DD/MM/YYYY hh:mm A');
  }

  onGlSearchFilter(event) {
    console.log(event)
		if (event != "") {
      let newData = this.allData.filter(item => {
        for (let field in item) {
          if (item[field] === null || item[field] === undefined) {
            continue;
          }
          if (item[field].toString().toLowerCase().indexOf(event.toString().toLowerCase()) !== -1) {
            return item;
          }
        }
      })

      this._programList.next(newData);
		} else {
      this.fullProgramList = this.allData;
      this._programList.next(this.fullProgramList);
    }

	}

  exportData(event) {

  }

  isProgramEmpty() {
    return true
  }

  getProgramKeys() {
    return ''
  }

  getProgramBatch(id) {
    var data = this.programBatchList.filter(item => {
      return item.id == id
    })

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].name;
    }
  }

  getProgramStyle(id) {
    var data = this.programStyleList.filter(item => {
      return item.id == id
    })

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].name;
    }
  }

  ngOnInit(): void {

    let batchParams = {
      UserId: this.sessionService.userId,
      ClientId: this.sessionService.clientId
    }

    this.batchService.getBatchCategoryList(batchParams).subscribe((res:any) => {
      if(res.value)
      this.programBatchList = res.value;

    });

    let styleParams = {
      UserId: this.sessionService.userId,
      ClientId: this.sessionService.clientId
    }

    this.styleService.getStyleList(styleParams).subscribe((res:any) => {
      if(res.value)
      this.programStyleList = res.value;
    });

    // Load the notifications
    let res = [
      {
        "id": 1,
        "programId": 1,
        "programBatchId": 2,
        "programStyleId": 1,
        "programPackageId": "123AS",
        "studentName": 'Ani',
        "className": "HipHop, Freestyle Seniors",
        "name": "Basic Style",
        "description": "some open program content",
        "startDate": "2021-03-06T08:57:32.270Z",
        "endDate": "2021-04-06T08:57:32.270Z",
        "duration": 3,
        "active": true,
        "lastModified": "2021-03-06T08:57:32.270Z",
        "modifiedBy": 1
      },
      {
        "id": 2,
        "programId": 1,
        "programBatchId": 2,
        "programStyleId": 1,
        "programPackageId": "123AS",
        "studentName": 'Ani',
        "className": "HipHop, Freestyle Seniors",
        "name": "Basic Style",
        "description": "some open program content",
        "startDate": "2021-03-06T08:57:32.270Z",
        "endDate": "2021-04-06T08:57:32.270Z",
        "duration": 3,
        "active": true,
        "lastModified": "2021-03-06T08:57:32.270Z",
        "modifiedBy": 1
      },
      {
        "id": 3,
        "programId": 1,
        "programBatchId": 2,
        "programStyleId": 1,
        "programPackageId": "123AS",
        "studentName": 'Ani',
        "className": "HipHop, Freestyle Seniors",
        "name": "Basic Style",
        "description": "some open program content",
        "startDate": "2021-03-06T08:57:32.270Z",
        "endDate": "2021-04-06T08:57:32.270Z",
        "duration": 3,
        "active": true,
        "lastModified": "2021-03-06T08:57:32.270Z",
        "modifiedBy": 1
      }
    ];

    this.fullProgramList = res;
    this.allData = res;
    this._programList.next(res);

    this.programs$.pipe( filter(res => res != null),
    ).subscribe((res:any) => {

      this.totalItems = res.length;
      this.ItemStartIndex = 0;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

    })

    // Subscribe to search input field value changes
    this.searchData.valueChanges
    .pipe(
      startWith(''),
      map((val) => {

        let newData = this.fullProgramList.filter(item => {
          for (let field in item) {
            if (item[field] === null || item[field] === undefined) {
              continue;
            }
            if (item[field].toString().toLowerCase().indexOf(val.toString().toLowerCase()) !== -1) {
              return item;
            }
          }
        })

        this._programList.next(newData);


      })
    )
    .subscribe();

    this.deleteSubscribe = this.modalsService.deleteindexcast.subscribe((id:any) => {

      if(id != null) {

        this.fullProgramList = this.fullProgramList.filter(item => {
          return item.id != id
        })
        this._programList.next(this.fullProgramList);

      }

    })

  }

  ngOnDestroy(): void
    {
        // Unsubscribe from all subscriptions
        this._unsubscribeAll.next();
        this._unsubscribeAll.complete();
        this.deleteSubscribe.unsubscribe();
    }

}
